import styled from "styled-components"

export const Icon = styled.i`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  svg {
    fill: gray;
  }
`
export const ClickArea = styled.div`
  position: absolute;
  cursor: pointer;
  display: flex;
  width: 36px;
  height: 36px;
`
